.featureArea {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.featureArea__row {
    width: 100%;
    display: grid;
    grid-gap: 2em;
    grid-template-columns: repeat(2, minmax(40%, 1fr));
    grid-auto-flow: dense;
}

.featureArea__row__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
    box-sizing: border-box;
    overflow: hidden;
}

.featureArea__row__item__imgBlock {
    position: relative;
    display: flex;
    width: 100%;
    box-sizing: border-box;
    overflow: hidden;
}

.featureArea__row__item__imgBlock img {
    width: 100%;
    object-fit: contain;
    filter: brightness(0.8);
    transition: all 0.3s linear;
}

.featureArea__row__item__imgBlock img:hover {
    filter: brightness(1);
    transform: scale(1.2);
}

.featureArea__row__item__title {
    margin: 5.6% 0 1.88% 0;
    font-size: 1.43em;
    color: #222222;
}

.featureArea__row__item__book {
    margin: 0;
    font-size: 1em;
    font-weight: 400;
    transition: all 0.3s linear;
    cursor: pointer;
}

.featureArea__row__item__book:hover {
    color: #0093df;
}

/*MEDIA*/

@media screen and (max-device-width: 540px) {

    .featureArea__row__item__title {
        margin: 5.6% 0 1.88% 0;
        line-height: 1em;
        font-size: 3em;
        color: #222222;
    }

    .featureArea__row__item__book {
        font-size: 2.5em;
        line-height: 1em;
        color: #0093df;
        margin: 0 0 2% 0;
    }
}