.markerLabel {
    position: absolute;
    top: 40%;
    left: 1em;
    width: 100%;
    line-height: 1.2em;
    padding: 2% 5%;
}

.contactPage {
    position: relative;
    background: #f9f9ff;
}

.contactPage__infoBlock {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    padding: 9% 0 0 0;
}

.contactPage__infoBlock__contacts {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 22%;
}

.contactPage__infoBlock__contacts__item {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
}

.contactPage__infoBlock__contacts__item img {
    width: 10%;
    object-fit: contain;
}

.contactPage__infoBlock__contacts__item__titlesBlock {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 80%;
}

.contactPage__infoBlock__contacts__item__titlesBlock__title {
    font-size: 1.14em;
    font-weight: 600;
    line-height: 1.2em;
    color: #222222;
    margin: 0;
}

.contactPage__infoBlock__contacts__item__titlesBlock__subtitle {
    font-size: 1em;
    font-weight: 400;
    line-height: 1.8;
    margin: 0 0 10% 0;
}

.contactPage__infoBlock__form {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-content: space-between;
    width: 66%;
}

.contactPage__infoBlock__form__inputsBlock {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 48%;
    box-sizing: border-box;
}

.contactPage__infoBlock__form__inputsBlock input {
    width: 100%;
    font-size: 0.93em;
    line-height: 1.2em;
    padding: 3.3% 5%;
    margin: 0 0 5% 0;
    border: 1px solid #ced4da;
    box-sizing: border-box;
}

.contactPage__infoBlock__form__textareaBlock {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: end;
    width: 48%;
}

.contactPage__infoBlock__form__textareaBlock textarea {
    width: 100%;
    resize: none;
    font-family: "Roboto", sans-serif;
    font-size: 0.93em;
    font-weight: 400;
    line-height: 1.2em;
    padding: 3.3% 5%;
    margin: 0 0 0 0;
    border: 1px solid #ced4da;
    box-sizing: border-box;
    height: 12.2em;
}

.contactPage__infoBlock__form__textareaBlock button {
    width: 47%;
    font-size: 1em;
    font-weight: 500;
    color: white;
    font-family: "Roboto", sans-serif;
    line-height: 1.2em;
    background: #0093df;
    border: 1px solid transparent;
    cursor: pointer;
    box-sizing: border-box;
    padding: 3.6% 5%;
    margin: 5% 0 0 0;
    transition: all 0.3s linear;
}

.contactPage__infoBlock__form__textareaBlock button:hover {
    color: #0093df;
    background: white;
    border: 1px solid #0093df;
}

/*MEDIA*/

@media screen and (max-device-width: 540px) {
    .contactPage__infoBlock {
        flex-direction: column;
        padding: 9% 0 0 0;
    }

    .contactPage__infoBlock__contacts {
        width: 100%;
    }

    .contactPage__infoBlock__contacts__item img {
        width: 8%;
    }

    .contactPage__infoBlock__contacts__item__titlesBlock__title{
        font-size: 2.85em;
        line-height: 1.2em;
    }

    .contactPage__infoBlock__contacts__item__titlesBlock__subtitle {
        font-size: 2.5em;
        line-height: 1.8;
    }

    .contactPage__infoBlock__form {
        flex-direction: column;
        width: 100%;
    }

    .contactPage__infoBlock__form__inputsBlock {
        width: 100%;
    }

    .contactPage__infoBlock__form__inputsBlock input {
        font-size: 2.325em;
        line-height: 1.2em;
    }

    .contactPage__infoBlock__form__textareaBlock {
        width: 100%;
    }

    .contactPage__infoBlock__form__textareaBlock textarea {
        font-size: 2.235em;
        line-height: 1.2em;
        height: 12em;
    }

    .contactPage__infoBlock__form__textareaBlock button {
        font-size: 2.5em;
        line-height: 1.2em;
    }
}